import axios from "axios";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setLoading } from "../../reducers/commonReducer";

const ShowDBPopUpComponent = () => {
  const API_URL = process.env.REACT_APP_API_URL
    ? process.env.REACT_APP_API_URL
    : "";
  const dispatch = useDispatch();
  const [dbList, setDbList] = useState([]);
  const { userdetails } = useSelector((state) => state.user);
  const getDBList = async () => {
    dispatch(setLoading(true));
    const { data } = await axios.get(`${API_URL}/api/getDB/list`);
    if (!data.error) {
      setDbList(data);
    } else {
      setDbList([]);
    }
    dispatch(setLoading(false));
  };
  const handleExportClick = async (dbName) => {
    try {
      const config = { headers: { "Content-type": "application/json" } };
      const response = await axios.post(
        `${API_URL}/api/getDB/downloadDB`,
        { token: userdetails.token, dbName },
        config
      );
      if (response.ok) {
        const blob = await response.blob();
        const url = window.URL.createObjectURL(blob);
        const a = document.createElement("a");
        a.href = url;
        a.download = "exported_data.json";
        a.click();
        window.URL.revokeObjectURL(url);
      }
    } catch (error) {
      console.error("Error exporting data", error);
    }
  };
  useEffect(() => {
    getDBList();
  }, []);
  return (
    <>
      {dbList && dbList.length > 0 && userdetails.isAdmin ? (
        <div className="container p-md-5">
          <table className="table table-hover table-striped table-dark">
            <tbody>
              {dbList.map((item, index) => (
                <tr className="p-color" key={index}>
                  <td>{item.name}</td>
                  <td>
                    <div
                      className="btn btn-secondary btn-sm cursor-pointer float-end"
                      onClick={(e) => handleExportClick(item.name)}
                    >
                      Download
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      ) : (
        "Show DB List"
      )}
    </>
  );
};

export default ShowDBPopUpComponent;
