// import { LoaderSM } from "./loader";
import {
  createAcademyReducer,
  updateAcademyReducer,
} from "../reducers/academyReducer";
import { useDispatch, useSelector } from "react-redux";
import FormElementComponent from "./FormElementComponent";

const AddAcademyComponentModal = (props) => {
  const { userdetails } = useSelector((state) => state.user);
  const dispatch = useDispatch();
  const formField = props.formField;
  const formInputValues = props.formInputValues;

  const closeModal = () => {
    props.setAddAcademyModal(false);
  };
  const createData = async (inputData) => {
    const data = await dispatch(
      createAcademyReducer({
        ...inputData,
        token: userdetails.token,
        id: formInputValues._id ? formInputValues._id : null,
      })
    );
    if (data.payload) {
      props.setAddAcademyModal(data, "created");
    }
  };
  const updateData = async (inputData) => {
    const data = await dispatch(
      updateAcademyReducer({
        ...inputData,
        token: userdetails.token,
        id: formInputValues._id ? formInputValues._id : null,
      })
    );
    if (data.payload) {
      props.setAddAcademyModal(data, "updated");
    }
  };

  return (
    <FormElementComponent
      formField={formField}
      formInputValues={formInputValues}
      modalHeading={
        formInputValues.academyName ? "Edit Academy" : "Add New Academy"
      }
      buttons={[
        {
          type: "cancel",
          title: "Cancel",
        },
        {
          type: formInputValues.academyName ? "update" : "create",
          title: formInputValues.academyName
            ? "Update Academy"
            : "Add New Academy",
        },
      ]}
      closeModal={closeModal}
      createData={createData}
      updateData={updateData}
      formType="modal"
    />
  );
};

export default AddAcademyComponentModal;
